@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;600&display=swap');

* {
    box-sizing: border-box;
}

html {
    height: 100%;
}

body {
    font-family: "Nunito", sans-serif;
    margin: 0;
}

.main-header {
    height: 6.8rem;
    min-width: 75rem;
    display: flex;
    justify-content: center;
    background-color: #4F6009;
    box-shadow: 0px 10px 20px #00000029;
}

.main-header__box {
    min-width: 75rem;
    max-width: 75rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.main-header__brand {
    position: relative;
    top: 3.5rem;
    left: 0.5rem;
    text-decoration: none;
}

.main-nav__items {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    align-items: center;
}

.main-nav__item {
    margin: 0 0 0 2.5rem;
}
  
.main-nav__item a {
    text-decoration: none;
    color: #DDD8A7;
    font-size: 1rem;
    font-weight: 600;
}